.section {
  background-color: #fff;
  padding-top: 4%;
  padding-bottom: 3%;
  position: relative;
  font-size: 0;
  &__title {
    text-align: center;
    margin-bottom: 30px;
    position: relative;
    .title {
      display: inline-block; }
    &.has-no-mb {
      margin-bottom: 0; }
    &.is-left {
      text-align: left; }
    &.has-line {
      position: relative;
      &::before {
        content: '';
        height: 1px;
        width: 53px;
        background-color: $c-text;
        position: absolute;
        bottom: -15px;
        left: 0; } } }
  &__links-list {
    display: flex;
    margin-bottom: 35px;
    @include md {
      margin-bottom: 20px; }
    .links-list {
      &:not(:last-child) {
        margin-right: 50px; } } }
  &__block {
    text-align: justify;
    &:not(:last-child) {
      margin-bottom: 50px;
      @include md {
        margin-bottom: 30px; } }
    p {
      font-size: 15px;
      margin-bottom: 35px;
      @include light;
      @include md {
        margin-bottom: 20px; }
      strong {
        @include bold; }
      a {
        color: #025db9;
        text-decoration: underline; }
      &.has-no-mb {
        margin-bottom: 0; } } }

  &--bg {
    @include cover;
    @include md {
      background-image: none !important; }
    .container {
      position: relative;
      z-index: 1; } }
  &--pt-sm {
    padding-top: 1%; }
  &--bd-top {
    @include xs {
      border-top: 1px solid #e6e6e6; } } }

.car-bg {
  @include cover;
  background-size: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
  width: 36.5%;
  z-index: 0; }
