.hero {
  background-color: #202020;
  font-size: 0;
  &__slider {
    .slide {
      height: calc(100vh - 90px); // header height
      @include lg {
        height: calc(100vh - 45px); } } }
  &__title {
    position: relative;
    padding-bottom: 15px;
    margin-bottom: 15px;
    .hero__subttl {
      margin-bottom: 0; }
    .title {
      margin-bottom: 5px; }
    &::before {
      content: '';
      position: absolute;
      width: 53px;
      height: 1px;
      background-color: #fff;
      bottom: 0;
      left: 0; } }
  &__subttl {
    font-size: 15px;
    color: #fff;
    text-transform: uppercase;
    margin-bottom: 15px; }
  &__btn-right {
    height: 100%;
    display: flex;
    align-items: center;
    @include md {
      padding-top: 15px;
      display: block; } }

  &--sm {
    @include cover;
    padding-top: 40px;
    padding-bottom: 40px;
    @include lg {
      padding-top: 80px;
      padding-bottom: 80px; } } }
