}

.custom-select__panel-item .icon {
  position: absolute;
  top: 50%;
  transform: translate(0, -50%);
  left: 20px;
  fill: currentColor;
  pointer-events: none;
}

.select--multiple .custom-select__opener {
  height: 48px;
  padding: 13px 20px;
  padding-right: 40px;
  font-size: 15px;
  color: #414141;
}

.select--multiple .custom-select__opener::before {
  top: 17px;
  right: 20px;
}

.select--multiple .custom-select__panel {
  padding: 20px;
  padding-top: 50px;
  max-height: none;
}

.select--multiple .custom-select__panel-item {
  position: absolute;
  width: 100%;
  top: 20px;
  padding-left: 15px;
  padding-right: 15px;
  z-index: 1;
  left: 0;
}

.select--multiple .custom-select__option {
  padding-top: 10px;
  padding-bottom: 10px;
  font-size: 15px;
}

.items-top-select {
  display: flex;
  align-items: center;
}

.items-top-select label {
  margin-right: 12px;
  font-size: 15px;
}

@media only screen and (max-width: 1199px) {
  .items-top-select label {
    display: none;
  }
}

.items-top-select .select {
  width: 100%;
}

.range-slider .noUi-handle {
  border-radius: 50%;
  width: 12px;
  height: 12px;
  background-color: #10ad44;
  border: 0;
  box-shadow: none;
  top: -5px;
  cursor: pointer;
}

.range-slider .noUi-handle::before, .range-slider .noUi-handle::after {
  display: none;
}

.range-slider .noUi-handle-lower {
  right: -8px !important;
}

.range-slider .noUi-handle-upper {
  right: -8px !important;
}

.range-slider .noUi-horizontal {
  height: 3px;
  box-shadow: none;
  border: 0;
  border-radius: 0;
  background-color: #414141;
}

.range-slider .noUi-horizontal::before, .range-slider .noUi-horizontal::after {
  content: '';
  position: absolute;
  top: -5px;
  display: inline-block;
  border-radius: 50%;
  width: 12px;
  height: 12px;
  background-color: #636363;
  pointer-events: none;
}

.range-slider .noUi-horizontal::before {
  left: -4px;
}

.range-slider .noUi-horizontal::after {
  right: -8px;
}

.range-slider .noUi-connects {
  border-radius: 0;
}

.range-slider .noUi-connect {
  border-radius: 0;
  background-color: #10ad44;
  cursor: pointer;
}

.range-slider__title {
  font-family: 'helveticaneuecyr-light', Arial, sans-serif;
  font-size: 15px;
  margin-bottom: 5px;
}

.breadcrumbs {
  font-size: 15px;
}

.breadcrumbs__item {
  display: inline-block;
}

.breadcrumbs__link {
  color: #fff;
  font-size: 15px;
  font-family: 'helveticaneuecyr-light', Arial, sans-serif;
}

.breadcrumbs__link.is-active {
  color: #9b9b9b;
  pointer-events: none;
}
