.form {
  &--consultation {
    .form {
      &__row {
        display: flex;
        margin-bottom: 40px;
        @include lg {
          display: block; } }
      &__field {
        @include lg {
          &:not(:last-child) {
            margin-bottom: 10px; } }
        &:not(:last-child) {
          margin-right: 60px; } } } } }
.input {
  @include light;
  input {
    height: 32px;
    border-bottom: 1px solid  #202020;
    padding-top: 5px;
    padding-bottom: 5px;
    min-width: 282px;
    font-family: inherit;
    @include placeholder {
      color: #979797;
      font-size: 15px;
      font-family: inherit;
      @include light; } } }

